.modal.feedback-modal .modal-body {
  padding: 0;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-color: #252525;
}
.modal.feedback-modal .modal-body-container {
  padding-top: 60px;
  padding-bottom: 60px;
}
@media (max-width: 767px) {
  .modal.feedback-modal .modal-body-container {
    padding-top: 30px;
  }
}
.modal.feedback-modal .modal-body-container .text-block {
  padding: 0 60px;
}
@media (max-width: 767px) {
  .modal.feedback-modal .modal-body-container .text-block {
    padding: 0 30px;
  }
}
.modal.feedback-modal .modal-body-container .text-block .title {
  margin-bottom: 15px;
  color: #fff;
  font-size: 36px;
  font-weight: 800;
  line-height: 1.1;
  text-transform: uppercase;
}
@media (max-width: 767px) {
  .modal.feedback-modal .modal-body-container .text-block .title {
    font-size: 24px;
  }
}
.modal.feedback-modal .modal-body-container .text-block .sub-title {
  color: #7c7c7c;
  font-size: 16px;
  font-weight: 400;
  text-transform: uppercase;
}
.modal.feedback-modal .modal-body-container form {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin-top: 25px;
}
@media (min-width: 320px) and (max-width: 991px) {
  .modal.feedback-modal .modal-body-container form {
    align-items: flex-start;
  }
}
@media (min-width: 768px) and (max-width: 991px) {
  .modal.feedback-modal .modal-body-container form {
    padding: 0 60px;
  }
}
@media (max-width: 767px) {
  .modal.feedback-modal .modal-body-container form {
    padding: 0 30px;
  }
}
@media (max-width: 575px) {
  .modal.feedback-modal .modal-body-container form {
    width: 100%;
    padding: 0 10px;
  }
}
.modal.feedback-modal .modal-body-container .form-item {
  margin-bottom: 30px;
}
@media (max-width: 575px) {
  .modal.feedback-modal .modal-body-container .form-item {
    width: 100%;
  }
}
.modal.feedback-modal .modal-body-container .form-item input {
  width: 530px;
  height: 60px;
  padding: 15px;
  background-color: transparent;
  color: #fff;
  border-right: none;
  border-left: none;
  border-top: none;
  border-bottom: 1px solid #434343;
  outline: none;
}
@media (min-width: 576px) and (max-width: 767px) {
  .modal.feedback-modal .modal-body-container .form-item input {
    width: 400px;
    height: 60px;
  }
}
@media (max-width: 575px) {
  .modal.feedback-modal .modal-body-container .form-item input {
    width: 100%;
    height: 60px;
  }
}
.modal.feedback-modal .modal-body-container .form-item-submit {
  box-shadow: 0 31px 49px rgba(245,38,63,0.44);
  align-self: flex-start;
  margin-left: 60px;
  margin-top: 50px;
}
@media (max-width: 767px) {
  .modal.feedback-modal .modal-body-container .form-item-submit {
    margin-top: 20px;
    margin-left: 30px;
  }
}
.modal.feedback-modal .modal-body-container .form-item-submit input {
  padding: 25px 45px;
  color: #fff;
  font-size: 24px;
  font-weight: 800;
  line-height: 1;
  text-transform: uppercase;
  background-color: #f5263f;
  border: none;
  cursor: pointer;
}
@media (min-width: 1201px) and (max-width: 1425px) {
  .modal.feedback-modal .modal-body-container .form-item-submit input {
    padding: 15px;
    font-size: 18px;
  }
}
@media (min-width: 992px) and (max-width: 1199px) {
  .modal.feedback-modal .modal-body-container .form-item-submit input {
    padding: 15px;
    font-size: 20px;
  }
}
@media (min-width: 320px) and (max-width: 991px) {
  .modal.feedback-modal .modal-body-container .form-item-submit input {
    padding: 20px;
    font-size: 18px;
  }
}
.header-modal .modal-dialog {
  margin: 0;
  padding: 0;
  max-width: 100%;
}
.header-modal .modal-dialog .modal-content {
  border-radius: 0;
  background-color: #f5263f;
}
.header-modal .modal-dialog .modal-content .modal-header {
  justify-content: space-between;
  align-items: center;
  padding: 25px 1rem 1rem 1rem;
  border-bottom: none;
}
.header-modal .modal-dialog .modal-content .modal-header .close {
  opacity: 1;
  padding: 0;
}
.header-modal .modal-dialog .modal-content .modal-header .close * {
  color: #fff;
}
.header-modal .modal-dialog .modal-content .fa-times-circle {
  color: #fff;
  font-size: 24px;
}
.header-modal .modal-dialog .modal-content .logo {
  color: #fff;
  font-size: 20px;
  font-weight: 900;
  line-height: 1;
}
.header-modal .modal-dialog .modal-body-container {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.header-modal .modal-dialog .modal-body-container .item-title {
  margin-bottom: 30px;
}
.header-modal .modal-dialog .modal-body-container .item-title *,
.header-modal .modal-dialog .modal-body-container .item-title {
  color: #fff;
  font-size: 16px;
  font-weight: 400;
  line-height: 1;
  letter-spacing: 2.91px;
  text-align: right;
  cursor: pointer;
}
.header-modal .modal-dialog .modal-body-container .item-title *:hover,
.header-modal .modal-dialog .modal-body-container .item-title:hover {
  color: $orange;
}
.header-modal .modal-dialog .modal-body-container .title-dropdown {
  position: relative;
}
.header-modal .modal-dialog .modal-body-container .title-dropdown:before {
  content: url("/img/icons/triangle.png");
  position: absolute;
  right: 100px;
  top: -3px;
}
.header-modal .modal-dialog .modal-body-container .title-dropdown ul.dropdown.show {
  display: block;
}
.header-modal .modal-dialog .modal-body-container .title-dropdown ul.dropdown {
  display: none;
  top: 10px;
  left: 0;
  padding: 0;
  margin: 0 0 20px 0;
}
.header-modal .modal-dialog .modal-body-container .title-dropdown ul.dropdown li {
  list-style-type: none;
  margin-bottom: 23px;
}
.header-modal .modal-dialog .modal-body-container .title-dropdown ul.dropdown li a {
  color: $orange;
  font-size: 14px;
  font-weight: 400;
  line-height: 1;
}
